.card-container{
    display: flex;
    flex-direction: row;
    background: linear-gradient(4deg, rgba(59, 130, 246, 0.00) 12.42%, rgba(59, 130, 246, 0.10) 63.32%), #FFF;
}

.card{
    width: 200px;
    height: 200px;
    border: 3px solid #DFECFC;
    margin: 10px;
    border-radius: 10px;
    cursor: pointer;   
}

.select{
    border: 3px solid #1059b3; 
}