.dropdown{
    width: 200px;

    margin-left: auto;
    margin-right: auto;

    cursor: pointer;

}

.dropdown-header{
    width: 100%;
    padding: 10px;
    position: relative;
    background-color: antiquewhite;
    border-radius: 2%;

    color: black;

    display: flex;
    box-sizing: border-box; 
} 

.action{
    position: absolute;
    right: 10px;
}

.dropdown-content{
    width: 100%;    

    margin-top: 10px;
    background-color: antiquewhite;

    display: flex;
    flex-direction: column;

    border-radius: 2%;
    overflow-y: auto;
    
    height: 0;
    transition: height 0.5s ease;
}

.open{
    height: 200px;
}

.item{
    padding: 10px;
    width: 100%;
    transition: all 0.3s ease;
}

.item:hover{
    background-color: azure;
}
