.navbar{
    position: relative;
    width: 100%;
    min-height: 50px;
    background-color: blue;
}

.nav-link {
    color: white;
    display: flex;
    flex-direction: row;

    margin: 10px;
    padding: 10px;
}

.nav-link li{
    list-style: none;
    padding: 10px;
}

.hamburger{
    display: none;
    flex-direction: column;
}

.hamburger-link {
    list-style: none;
    color: white;
    position: absolute;
    background-color: blue;
    width: 100%;
    top:50px;

    max-height: 0;
    overflow-y: auto;

    display: block;
    transition: all 1s ease;
}

.active{
    max-height: 115px;
}

.hamburger-link li{
    padding: 10px;
}

.hamburger-icon{
    cursor: pointer;
    position: absolute;
    right: 10px;
    color: white;
    width: 40px;
    height: 40px;
}


@media (max-width: 768px) {
    .nav-link{
        display: none;
    }

    .hamburger{
        display: flex;
    }
}